<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul
          class="cart-list"
          v-for="shopCart in cartInfoList"
          :key="shopCart.id"
        >
          <li class="cart-list-con1">
            <input
              type="checkbox"
              name="chk_list"
              :checked="shopCart.isChecked"
              @input="getShopCartSingleChecked(shopCart.skuId, $event)"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="shopCart.imgUrl" />
            <div class="item-msg">
              {{ shopCart.skuName }}
            </div>
          </li>
          <li class="cart-list-con3">
            <div class="item-txt">语音升级款</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ shopCart.skuPrice }}</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:;" class="mins" @click="changeNum(shopCart, -1)"
              >-</a
            >
            <input
              autocomplete="off"
              type="text"
              :value="shopCart.skuNum"
              minnum="1"
              class="itxt"
              @change="changeRegNum($event, shopCart)"
              @focus="changeOldNum(shopCart)"
            />
            <a href="javascript:;" class="plus" @click="changeNum(shopCart, 1)"
              >+</a
            >
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ shopCart.skuPrice * shopCart.skuNum }}</span>
          </li>
          <li class="cart-list-con7">
            <a
              href="javascript:;"
              class="sindelet"
              @click="delteShopCart(shopCart.skuId)"
              >删除</a
            >
            <br />
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" v-model="checkedAll" />
        <span>全选</span>
      </div>
      <div class="option">
        <a href="javascript:;" @click="deleteCheckAll">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">
          已选择 <span>{{ goodsAllNum }}</span
          >件商品
        </div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ goodsAllPrice }}</i>
        </div>
        <div class="sumbtn">
          <router-link to="/trade">结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reqShopCartList,
  reqShopCartSingleChecked,
  reqDeleteShopCart,
  reqAddShopCart,
  reqCheckedAll,
  reqDelelteCheckedAll,
} from "@/api";
export default {
  name: "ShopCart",
  data() {
    return {
      cartInfoList: [],
      oldskuNum: 1,
    };
  },
  mounted() {
    this.getShopCartData();
  },
  methods: {
    // 请求购物车
    async getShopCartData() {
      let res = await reqShopCartList();
      // ?是可选链操作符 当前面的值是false就不会往后执行了，只有前面有值才会继续往后执行
      // 给每个请求回来的数据添加一个isflag属性用于解决累加累减使用开关思想
      this.cartInfoList = res[0]?.cartInfoList.map((item) => {
        item.isflag = true;
        return item;
      });
    },

    // 发送请求切换购物车选中状态
    async getShopCartSingleChecked(skuId, e) {
      let isChecked = e.target.checked ? "1" : "0";
      try {
        let res = await reqShopCartSingleChecked(skuId, isChecked);
        if (res === null) this.getShopCartData();
      } catch (error) {
        alert(error.message);
      }
    },
    // 发送请求删除购物车单个商品
    async delteShopCart(skuId) {
      try {
        await reqDeleteShopCart(skuId);
        this.getShopCartData();
      } catch (error) {
        alert(error.message);
      }
    },
    // 累加累减
    async changeNum(shopCart, skuNum) {
      if (!shopCart.isflag) return;
      shopCart.isflag = false;
      if (shopCart.skuNum + skuNum < 1) {
        shopCart.isflag = true;
        return;
      }
      await reqAddShopCart(shopCart.skuId, skuNum);
      // 因为要等待请求数据的完成所以要加个await不然还是会在数据未设置前会发送两次
      await this.getShopCartData();
      shopCart.isflag = true;
    },
    // 删除选中的商品
    async deleteCheckAll() {
      try {
        const skuIdList = this.cartInfoList.reduce((prev, current) => {
          if (current.isChecked === 1) {
            prev.push(current.skuId);
          }
          return prev;
        }, []);
        let res = await reqDelelteCheckedAll(skuIdList);
        console.log(res);
      } catch (error) {
        alert(error.message);
      }
    },
    // 数量输入框 失去焦点后触发
    async changeRegNum(e, shopCart) {
      const reg = /^[\d]+$/g;
      // 如果没有通过正则校验
      if (!reg.test(e.target.value) || e.target.value < 1) {
        e.target.value = this.oldskuNum;
      }

      //如果通过了就发送请求
      let skuNum = e.target.value - shopCart.skuNum;
      await reqAddShopCart(shopCart.skuId, skuNum);
      this.getShopCartData();
    },
    // 数量输入框 获得焦点后触发
    changeOldNum(shopCart) {
      this.oldskuNum = shopCart.skuNum;
    },
  },
  computed: {
    checkedAll: {
      get() {
        return this.cartInfoList?.every((item) => {
          return item.isChecked === 1;
        });
      },
      async set(checked) {
        try {
          // 获取所有商品的id放在数组中
          const skuIdList = this.cartInfoList.map((item) => {
            return item.skuId;
          });
          await reqCheckedAll(skuIdList, checked ? 1 : 0);

          this.getShopCartData();
        } catch (error) {
          alert(error.message);
        }
      },
    },
    // 选中几件商品
    goodsAllNum() {
      return this.cartInfoList.reduce((prev, current) => {
        if (current.isChecked === 1) {
          prev += current.skuNum;
        }
        return prev;
      }, 0);
    },
    // 总价
    goodsAllPrice() {
      return this.cartInfoList.reduce((prev, current) => {
        if (current.isChecked === 1) {
          prev += current.skuNum * current.skuPrice;
        }
        return prev;
      }, 0);
    },
  },
};
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 4.1667%;
        }

        .cart-list-con2 {
          width: 25%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con3 {
          width: 20.8333%;

          .item-txt {
            text-align: center;
          }
        }

        .cart-list-con4 {
          width: 12.5%;
        }

        .cart-list-con5 {
          width: 12.5%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 12.5%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 12.5%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>